@mixin custom-cursor($image-path) {
  cursor: pointer;
  &:hover {
    cursor: url(#{$image-path}), auto;
  }
}

@mixin on-tablet {
  @media (min-width: 1280px) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: 640px) {
    @content;
  }
}

@mixin content-padding-inline() {
  padding-inline: 20px;

  @include on-tablet {
    padding-inline: 72px;
  }

  @include on-desktop {
    margin-inline: auto;
    padding-inline: 120px;
  }
}


@mixin hover($property, $toValue) {
  transition: #{$property} 0.3s;
  &:hover {
    #{$property}:$toValue;
  }
}

@mixin page-grid {
  --columns: 2;

  display: grid;
  gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include on-tablet {
    --columns: 6;

    column-gap: 24px;
  }

  @include on-desktop {
    --columns: 12;
  }
}
